.scroll-y {
  position: relative;
  height: 100%;
  overflow-x: hidden;
}
.scroll-y[data-moving] {
  user-select: none;
}
.scroll-y .scroll-y__scrollbar-hider {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
.scroll-y .scroll-y__scrollbar-container {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 10px;
  overflow-y: hidden;
}
.scroll-y .scroll-y__scrollbar {
  position: absolute;
  width: 8px;
  top: 8px;
  bottom: 8px;
  transform-origin: top;
  right: 1px;
  background-color: #fff;
  opacity: 0;
  transition: opacity 0.2s 0.5s ease;
}
.scroll-y .scroll-y__scrollbar > * {
  content: "";
  position: absolute;
  width: 8px;
  background-color: inherit;
  height: 8px;
  border-radius: 8px;
}
.scroll-y .scroll-y__scrollbar > *:first-child {
  top: -4px;
}
.scroll-y .scroll-y__scrollbar > *:last-child {
  bottom: -4px;
}
.scroll-y.dark .scroll-y__scrollbar {
  background-color: black;
}
.scroll-y:hover:not([data-hidden]) .scroll-y__scrollbar, .scroll-y[data-moving]:not([data-hidden]) .scroll-y__scrollbar, .scroll-y[data-scrolling]:not([data-hidden]) .scroll-y__scrollbar {
  opacity: 0.6;
  transition: opacity 0.5s ease;
}
.scroll-y[data-hidden] .scroll-y__scrollbar {
  transition: opacity 0s ease;
}
.scroll-y[data-moving]:not([data-hidden]) .scroll-y__scrollbar {
  opacity: 0.8;
}